#header {width: 100%;height: 156px;
    position: relative;z-index: 3;
    background: #fff;
    box-shadow: 0 3px 6px rgba(0,0,0,  0.07);
    .container {width: 100%;max-width: 1050px;height: 100%;margin: 0 auto;
        .row {width: 100%;
            &.row1 {height: 36px;display: flex;align-items: center;justify-content: flex-end;
                #aside {
                    ul {display: flex;
                        li {font-size: 12px;
                            a {color: rgb(51,51,51);                   
                                span {margin-left: 5px;
                                    img {width: 8px;}
                                }
                            }
                            &:first-child {
                                a {color: rgb(95,0, 128);font-weight: 500;}
                            }
                            i {color: #ccc;margin: 0 10px;}
                        }
                    }
                }
            }
            &.row2 {height: 64px;display: flex;align-items: center;
                > div {height: 48px;                    
                    &.left {width: 100%;max-width: 325px;
                        margin: 0 auto 0 0;
                        display: flex;align-items: center;
                        h1 {
                            span{margin-right: 20px;
                                img {width: 82px;}
                            }
                            a {color: rgb(95,0, 128);font-size: 18px;font-weight: 600;}
                        }
                        i {color: #ccc;margin: 0 10px;}
                        > a {color:rgb(181,181,181);font-size: 18px;font-weight: 600;}
                    }
                    &.center {width: 100%;min-width: 250px;max-width: 400px;
                        margin: 0 auto;
                        position: relative;
                        input {width: 100%;height: 100%;
                            position: relative;z-index: 1;
                            border: 1px solid rgb(95,0, 128);
                            font-size: 16px;
                            border-radius: 6px;
                            padding-left: 14px;
                            color: rgb(51,51,51);
                            &::placeholder {color:#999;}
                        }
                        button {width: 46px;height: 100%;
                            position: absolute;top: 0;right: 0;z-index: 2;
                            background: transparent;
                            border: 0;
                            cursor: pointer;
                            img {width: 36px;}
                        }
                    }
                    &.right {width: 100%;max-width: 325px;
                        display: flex;align-items: center;justify-content: flex-end;
                        a {margin:10px;
                            img {width: auto;}
                        }
                    }
                }
            }
            &.row3 {height: 56px;display: flex;justify-content: space-between;
                > div {height: 100%;display: flex;align-items: center;
                    &.left {width: 100%;max-width: 225px;justify-content: flex-start;
                        h1 {
                            a {                                
                                img {width: auto;}
                                span {color: rgb(90,90,90); margin-left: 10px;
                                    font-weight: 600;font-size: 16px;
                                }
                                &:hover {
                                    span {color: rgb(95,0,128);}
                                }
                            }
                        }
                    }
                    &.center {width: 100%;max-width: 600px;
                        #nav {width: 100%;
                            ul {width: 100%;display: flex;
                                li {width: 25%;text-align: center;
                                    a {color: rgb(90,90,90);
                                       font-size: 16px;font-weight: 600;
                                       padding-bottom: 1px;
                                       border-bottom: 1px solid transparent;
                                       &:hover {color: rgb(95,0,128);border-color: rgb(95,0, 128);}
                                    }
                                }
                            }
                        }
                    }
                    &.right {width: 100%;max-width: 225px;
                        display: flex;justify-content: flex-end;
                        a {color: rgb(102,102, 102);
                            margin-left: 5px;
                            font-size: 13px;
                            padding: 8px 14px;
                            border: 1px solid #eee;
                            border-radius: 16px;
                            b {color: rgb(95,0, 128);}
                        }
                    }
                }
            }
        }
    }
}