#topModal {width: 100%;height: 42px;background: rgba(95,0, 128, 1);
    .container {width: 100%;max-width: 1050px;height: 100%;
        position: relative;
        margin: 0 auto;
        > a {width: 100%;height: 100%;
            position: relative;z-index: 1;
            display: flex;align-items: center;justify-content: center;
            color: #fff;
        }
        button {width: 42px;height: 100%;
            position: absolute;top:0;right:0;z-index: 2;
            background: transparent;
            border: 0;
            cursor: pointer;
            svg {width: 20px;height: 20px;}
        }
    }
}